
import {GET_ITEMS,ADD_ITEM,DELETE_ITEM,LOADING_ITEMS,ADD_TO_CART } from '../actions/type'
const initialState = {
    items:[],
    loading:false
}
export default function (state = initialState ,action) {
    switch (action.type) {
        case GET_ITEMS:
            return{ 
                ...state,
                items: action.payload,
                loading: false
            }; 
        case DELETE_ITEM:
            return {
                ...state,
                items: state.items.filter(item => item._id !== action.payload)
            }  
        case ADD_TO_CART:
            return {
                ...state,
                items: state.items.map(product =>{
                    if (product._id === action.payload._id) {
                        const item = {...product, inCart: action.payload.inCart}
                        return item
                    }
                    return product
                })
            }     
        case ADD_ITEM:
            return{
                ...state,
                items:[action.payload,...state.items]
            }
        case LOADING_ITEMS: 
            return{
                ...state,
                loading: true
            }          
        default:
            return state
    }
} 