import React from 'react'

export default function CartItem({cartItem ,value}) {
    // console.log(cartItem ,value)
    const{increment, decrement, removeInCart} = value
    const {_id, name, img, price , count ,total} = cartItem                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    
    return (
       <div className="row my-1 text-capitalize text-center">
           <div className="col-10 mx-auto col-lg-2"> 
               <img 
                src={img}
                alt="product"
                className="img-fluid"
                style={{width:"5rem", height:"5rem"}}
                />
           </div>
           <div className="col-10 mx-auto col-lg-2">
           <span className="d-lg-none">product :</span>
               { name }
           </div>
            <div className="col-10 mx-auto col-lg-2">
                <span className="d-lg-none">price :</span>
                { price }
            </div>
            <div className="col-10 mx-auto col-lg-2 my-2 my-lg-0">
               <div className="d-flex justify-content-center">
                    <div>
                        <span className="btn btn-black mx-1" onClick ={() => decrement(_id)}>-</span>
                        <span className="btn btn-black mx-1" >{ count }</span>
                        <span className="btn btn-black mx-1" onClick ={() => increment(_id)} >+</span>
                    </div> 
               </div>
            </div>
            <div className="col-10 mx-auto col-lg-2">
                <div className="cart-icon" onClick ={() => removeInCart(_id)}>
                    <i className="fas fa-trash"></i>
                </div>
                
            </div>
            <div className="col-10 mx-auto col-lg-2"> 
                <span>item total : ${ total } </span>
            </div>
       </div>
    )
}
